.black-screen {
  z-index: 1000;
  position: absolute;
  top: 0;
  bottom: 0;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  animation: fade 2s;
  -webkit-animation: fade 2s;
  transition-timing-function: linear;
  -webkit-transition-timing-function: linear;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
}

.black-buttons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
  width: 60%;
}

.google-form {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.google-form > button {
  border: none;
  border-radius: 30px;
  padding: 10px 15px;
  background-color: var(--basic-restart-btn);
  font-size: 1.875em !important;
  cursor: pointer;
  user-select: none;
}

.google-form > button:hover {
  background-color: var(--basic-restart-btn-hover);
}

@keyframes fade {
  0% {
    opacity: 0%;
  }

  100% {
    opacity: 100%;
  }
}

@media (max-width: 620px) {
  .black-buttons {
    flex-direction: column;
  }
}
