.App-bg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: var(--basic-bg);
}

.App {
  display: grid;
  grid-auto-flow: column;
  width: 80%;
  height: 80%;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.select-none {
  user-select: none;
}

.hidden {
  visibility: hidden;
}

.not-found {
  margin: 300px 0;
  letter-spacing: 1px;
  text-align: center;
}

@media (max-width: 800px) {
  .App {
    height: 100%;
  }
}
