.google-form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.google-form-container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  border-radius: 30px;
  padding: 20px;
  min-width: 450px;
  width: 60%;
  background-color: var(--basic-restart-btn);
}

.google-form-container > div {
  width: 100%;
}

.google-checkbox-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.google-checkbox {
  border: 2px solid var(--basic-restart-btn-hover);
  border-radius: 10px;
  width: 100%;
  padding: 20px;
  cursor: pointer;
}

.google-checkbox:hover {
  background-color: var(--basic-restart-btn-hover);
}

.google-checkbox.is-checked {
  border-color: var(--basic-choose-profile-text);
}

.google-long-answer textarea {
  border: 2px solid var(--basic-restart-btn-hover);
  border-radius: 10px;
  padding: 10px 15px;
  width: 100%;
}

.google-form-container button {
  padding: 5px 10px;
  font-size: 1.875em !important;
  cursor: pointer;
  user-select: none;
}

@media (max-width: 620px) {
  .google-form-container {
    gap: 15px;
    border-radius: 20px;
    min-width: 400px;
    width: 80%;
  }

  .google-checkbox-container {
    flex-direction: column;
    justify-content: flex-start;
  }

  .google-checkbox {
    border-radius: 10px;
    padding: 15px;
    width: 100%;
  }
}

@media (max-width: 420px) {
  .google-form-container {
    min-width: 250px;
  }
}
