.bar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
}

.bar-container img {
  margin-bottom: 50px;
  width: 50px;
  height: 50px;
  animation: slide 5s;
  -webkit-animation: slide 5s;
  transition-timing-function: linear;
  -webkit-transition-timing-function: linear;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  /* animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite; */
}

.loading-bar-base {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 25px;
  width: 500px;
  height: 20px;
  background: #e0e0eb;
}

.loading-bar {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 25px;
  width: 500px;
  height: 20px;
  background: #f5a0b3;
  animation: stretch 5s;
  -webkit-animation: stretch 5s;
  transition-timing-function: linear;
  -webkit-transition-timing-function: linear;
  animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  /* animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite; */
}

.bar-container h2 {
  margin-top: 0;
}

@keyframes slide {
  0% {
    transform: translateX(-250px);
  }

  100% {
    transform: translateX(250px);
  }
}

@keyframes stretch {
  0% {
    transform: translateX(-250px);
    width: 0;
  }

  /* 50% {
    background-color: #FA5570;
    width: 50%;
  } */

  100% {
    background-color: #ec5d57;
    width: 500px;
  }
}

@media (max-width: 620px) {
  .bar-container {
    width: 300px;
  }

  .bar-container img {
    margin-bottom: 50px;
    width: 40px;
    height: 40px;
  }

  .loading-bar-base {
    border-radius: 25px;
    width: 300px;
  }

  .loading-bar {
    border-radius: 25px;
    width: 300px;
  }

  @keyframes slide {
    0% {
      transform: translateX(-150px);
    }

    100% {
      transform: translateX(150px);
    }
  }

  @keyframes stretch {
    0% {
      transform: translateX(-150px);
      width: 0;
    }

    100% {
      background-color: #ec5d57;
      width: 300px;
    }
  }
}

@media (max-width: 360px) {
  .bar-container {
    width: 200px;
  }

  .bar-container img {
    margin-bottom: 40px;
    width: 30px;
    height: 30px;
  }

  .loading-bar-base {
    border-radius: 25px;
    width: 200px;
    height: 18px;
  }

  .loading-bar {
    border-radius: 25px;
    width: 200px;
    height: 18px;
  }

  @keyframes slide {
    0% {
      transform: translateX(-100px);
    }

    100% {
      transform: translateX(100px);
    }
  }

  @keyframes stretch {
    0% {
      transform: translateX(-100px);
      width: 0;
    }

    100% {
      background-color: #ec5d57;
      width: 200px;
    }
  }
}
