.profile {
  z-index: 600;
  aspect-ratio: 1/1;
  border-radius: 100px;
}

.profile img {
  aspect-ratio: 1/1;
  border-radius: 100px;
  width: 50px;
  height: 50px;
  object-fit: cover;
}

@media (max-width: 620px) {
  .profile img {
    width: 40px;
    height: 40px;
  }
}

@media (max-width: 420px) {
  .profile img {
    width: 30px;
    height: 30px;
  }
}
