.chats {
  padding-top: 30px;
  width: 100%;
  height: 85%;
  overflow-y: auto;
}

.chat:not(:last-child) {
  margin-bottom: 20px;
}

.chat.user {
  justify-content: flex-end;
}

.chat .profile {
  margin-right: 15px;
}

.bubble {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-radius: 2px 20px 20px 20px;
  padding: 0 15px;
  min-height: 50px;
  max-width: 80%;
  background-color: var(--basic-chat-bg);
  color: var(--basic-chat-text);
  word-break: break-all;
  overflow-wrap: break-all;
}

.bubble.user {
  justify-content: flex-end;
  border-radius: 20px 2px 20px 20px;
}

.bubble p {
  font-size: 14px !important;
}

@media (max-width: 620px) {
  .bubble {
    min-height: 40px;
  }

  .bubble {
    border-radius: 2px 13px 13px 13px;
  }

  .bubble.user {
    border-radius: 13px 2px 13px 13px;
  }
}

@media (max-width: 420px) {
  .bubble p {
    font-size: 12px !important;
  }
}
