.mobile {
  display: grid;
  grid-template-rows: 80px auto;
  grid-auto-flow: column;
  border-radius: 40px 40px 0 0;

  box-shadow:
    inset 0 -100px 40px -30px var(--basic-bg),
    0 -25px 20px -25px var(--basic-phone-shadow);
  /* -webkit-mask-image: linear-gradient(rgba(0,0,0, 1) 80%, rgba(0,0,0, 0) 95%);
  mask-image: linear-gradient(rgba(0,0,0, 1) 80%, rgba(0,0,0, 0) 95%);
  -webkit-mask-size: 100% 95%;
  mask-size: 100% 95%;
  mask-repeat: no-repeat; */

  aspect-ratio: 5/3;
  /* aspect-ratio: 16/9; */

  padding-bottom: 50px;
  width: 100%;
  max-width: 800px;
  background-color: var(--basic-phone);
  overflow-y: hidden;
}

.mobile > div {
  padding-left: 40px;
  padding-right: 40px;
}

.mobile-top {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  width: 100%;
  background-color: var(--basic-phone-top);
  color: var(--basic-phone-top-text);
  font-size: 1.875em;
  font-weight: bold;
}

.mobile-top img {
  width: 30px;
  height: 30px;
}

@media (max-width: 720px) {
  .mobile {
    grid-template-rows: 60px auto;
    border-radius: 30px 30px 0 0;
    aspect-ratio: 4/3;
    padding-bottom: 35px;
  }

  .mobile > div {
    padding-left: 40px;
    padding-right: 40px;
  }

  .mobile-top {
    gap: 5px;
    font-size: 0.875em;
  }

  .mobile-top img {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 600px) {
  .mobile {
    grid-template-rows: 50px auto;
    border-radius: 25px 25px 0 0;
    aspect-ratio: 4/5;
    padding-bottom: 30px;
  }

  .mobile > div {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-width: 420px) {
  .mobile > div {
    padding-left: 20px;
    padding-right: 20px;
  }
}
