.choose-person {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

.choose-person h1 {
  margin: 0;
  color: var(--basic-choose-profile-text);
  font-weight: bold;
  font-size: 5em;
}

.chat-profiles {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 70px;
}

.chat-profile {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  border: 5px solid var(--basic-choose-profile-bg);
  border-radius: 20px;
  background-color: var(--basic-choose-profile-bg);
  padding: 30px;
  width: 40%;
  aspect-ratio: 1/1;
  cursor: pointer;
}

.chat-profile p {
  margin: 0;
  color: var(--basic-choose-profile-text);
  font-weight: bold;
  font-size: 3em;
  word-break: break-all;
  overflow-wrap: break-all;
}

.chat-profile:hover {
  border-color: var(--choose-profile-hover);
}

.chat-profile img {
  width: 70%;
  height: auto;
  aspect-ratio: 1/1;
}

@media (max-width: 620px) {
  .choose-person {
    gap: 40px;
  }

  .choose-person h1 {
    font-size: 4em;
  }

  .chat-profiles {
    flex-flow: column nowrap;
    gap: 40px;
    width: 100%;
  }

  .chat-profile {
    width: 100%;
    padding: 15px;
    aspect-ratio: 16/9;
  }

  .chat-profile img {
    width: 50%;
  }

  .chat-profile p {
    font-size: 2.5em;
  }
}
