.selection-btn {
  z-index: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  width: 100%;
  padding: 20px;
  background-color: var(--basic-selection-btn);
  cursor: pointer;
}

.selection-btn:hover {
  background-color: var(--basic-selection-btn-hover);
}
